import React, {Component} from "react";
import VerifConnexion from '../verifConnexion.js';
import Sidebar from "../include/sidebar";
import Topbar from "../include/topbar";
import Footer from '../footer';
import LogoutModal from "../logoutmodal";

class Page extends Component{

    constructor(){
      super();
      this.state = {
        
      }
    }

    render(){
        return (
            <div className="vh-100">
                <VerifConnexion />
                {/* Page Wrapper */}
                <div id="wrapper">
        
                    <Sidebar />
        
                    {/* Content Wrapper */}
                    <div id="content-wrapper" className="d-flex flex-column">
        
                    {/* Main Content */}
                    <div id="content">
        
                        <Topbar />
        
                        {this.Contenu()}
        
                    </div>
                    {/* End of Main Content */}
        
                    <Footer />
        
                    </div>
                    {/* End of Content Wrapper */}
        
            </div>
            {/* End of Page Wrapper */}
        
            {/* Scroll to Top Button*/}
            <a className="scroll-to-top rounded" href="#page-top">
                <i className="fas fa-angle-up"></i>
            </a>
        
            <LogoutModal />
        
        </div>
            );
        
        }


        Contenu() {
            return (
                <div className="vh-100">
                    {/* Begin Page Content */}
                    <div className="container-fluid">   

                {/* Page Heading */}
                <div className="d-sm-flex align-items-center justify-content-between mb-4">
                <h1 className="h3 mb-0 text-gray-800">Accueil</h1>
                </div>

                
                <div className="row">

                {/* Content Column */}
                

                <div className="col-lg-6 mb-4">

                    {/* Illustrations */}
                    <div className="card shadow mb-4">
                    <div className="card-header py-3">
                        <h6 className="m-0 font-weight-bold text-primary">Illustrations</h6>
                    </div>
                    <div className="card-body">
                        <div className="text-center">
                        <img className="img-fluid px-3 px-sm-4 mt-3 mb-4" style={{width: '25rem'}} src="img/undraw_posting_photo.svg" alt="" />
                        </div>
                        <p>Lorem ipsum</p>
                        <a target="_blank" rel="nofollow" href="https://undraw.co/">Browse Illustrations on unDraw &rarr;</a>
                    </div>
                    </div>


                </div>

                <div className="col-lg-6 mb-4">
                    {/* Approach */}
                    <div className="card shadow mb-4">
                        <div className="card-header py-3">
                            <h6 className="m-0 font-weight-bold text-primary">Development Approach</h6>
                        </div>
                        <div className="card-body">
                            <p>SB Admin 2 makes extensive use of Bootstrap 4 utility classNamees in order to reduce CSS bloat and poor page performance. Custom CSS classNamees are used to create custom components and custom utility classNamees.</p>
                            <p className="mb-0">Before working with this theme, you should become familiar with the Bootstrap framework, especially the utility classNamees.</p>
                        </div>
                    </div>
                </div>

                </div>

                </div>
                {/* /.container-fluid */}
                    </div>
        )
    }
  }
  
  export default Page;
