import React from 'react';
import axios from 'axios';
import {
  Redirect
} from "react-router-dom";


export default function Logout () {

  let donnees = {token: JSON.parse(sessionStorage.getItem('tokenUser'))};

  let  adresseAPI = localStorage.getItem("adresseApi");

  axios.post(adresseAPI+`users_logout.php`, donnees, {headers: {'Content-Type':  'application/x-www-form-urlencoded'}})
  .then(function (reponse) {
      
  })
  .catch(function (error) {
      console.log("Erreur de conexion, l'api ne fonctionne pas");
  });

  sessionStorage.removeItem('infosUser');
  sessionStorage.removeItem('tokenUser');

  return (
      <Redirect
      to={{
      pathname: "/login",
      state: { from: "controller" }
      }}
      />
    )
    
}
