import React, {Component} from "react";
import axios from 'axios';

import {
  //BrowserRouter as Router,
  //Switch,
  //Route,
  //Link
} from "react-router-dom";
import VerifConnexion from '../verifConnexion.js';
import Sidebar from "../include/sidebar";
import Topbar from "../include/topbar";
import Footer from '../footer';
import LogoutModal from "../logoutmodal";

class Alerte extends Component{

    adresseAPI = localStorage.getItem("adresseApi");

    constructor(){
      super();
      this.state = {
        message: "",
        langue: "",
        nbreCible: "",
        description: "",
        baseDeDonnees : null,
        fichierAudio : null,

      }
    }

    onChange(event){
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    choixFichier(event){
        this.setState({
            [event.target.name]: event.target.files[0]
        });
    }

    async envoyer(event){
        event.preventDefault();
        let donnees = {
            donnees: this.state,
            type: "alerte"
        }
        var self = this;

        if(self.state.baseDeDonnees==null){
            self.setState({
                message: "Veuillez choisir le fichier comportant votre base de données d'utilisateur."
            });
            return 0
        }

        if(self.state.fichierAudio==null){
            self.setState({
                message: "Veuillez choisir le fichier audio à envoyer."
            });
            return 0
        }

        self.setState({message: ""});


        const form = new FormData();
        form.append('baseDeDonnees', this.state.baseDeDonnees);
        form.append('description', this.state.description);
        form.append('fichierAudio', this.state.fichierAudio);
        form.append('langue', this.state.langue);
        form.append('nbreCible', this.state.nbreCible);
        form.append('type', "alerte");
        form.append('token', sessionStorage.getItem('tokenUser'));

        
        axios.post(this.adresseAPI+`passer_commande.php`, form, {
            headers: {
                'content-type': `multipart/form-data; boundary=${form._boundary}`,
              }
            })
            .then(function (reponse) {
                
            console.log(reponse)
            
            if(reponse.data.status=="success")
            {  
                for (var property in self.state){
                    self.setState({[property] : ""})
                }

                self.setState({
                    message: reponse.data.message
                })
                
            }else{
                self.setState({
                    message: "Erreur lors de l'enregistrement dans la base de données."
                })
            }

            })
            .catch(function (error) {
                console.log("Erreur de connexion, l'api ne fonctionne pas");
                self.setState({
                message: "Erreur de connexion au serveur: veuillez-nous contacter!"
            })
            });
        
    }


    render(){
        return (
            <div className="vh-100">
                <VerifConnexion />
                {/* Page Wrapper */}
                <div id="wrapper">
        
                    <Sidebar />
        
                    {/* Content Wrapper */}
                    <div id="content-wrapper" className="d-flex flex-column">
        
                    {/* Main Content */}
                    <div id="content">
        
                        <Topbar />
        
                        {this.Contenu()}
        
                    </div>
                    {/* End of Main Content */}
        
                    <Footer />
        
                    </div>
                    {/* End of Content Wrapper */}
        
            </div>
            {/* End of Page Wrapper */}
        
            {/* Scroll to Top Button*/}
            <a className="scroll-to-top rounded" href="#page-top">
                <i className="fas fa-angle-up"></i>
            </a>
        
            <LogoutModal />
        
        </div>
            );
        
        }


        Contenu() {
            return (
                <div className="vh-100">
                    {/* Begin Page Content */}
                    <div className="container-fluid">   

                {/* Page Heading */}
                <div className="d-sm-flex align-items-center justify-content-between mb-4">
                <h1 className="h3 mb-0 text-gray-800">COMMANDES D'ALERTE VOCALE (audio) </h1>
                </div>

                
                <div className="row">

                {/* Content Column */}
                

                <div className="col-sm-6 mb-4">

                    {/* Illustrations */}
                    <div className="card shadow mb-4">
                    <div className="card-header py-3">
                        <h6 className="m-0 font-weight-bold text-primary">Commande</h6>
                    </div>
                    <div className="card-body">
                    <form className="user">
                    <div className="form-group">
                        <input type="text" value={this.state.nbreCible} name="nbreCible" className="form-control"  placeholder="Nombre de personnes cible par l'alerte" onChange={this.onChange.bind(this)} />
                    </div>

                    <div className="form-group">
                        <textarea value={this.state.description} type="text" name="description" className="form-control"  placeholder="Description" onChange={this.onChange.bind(this)} />
                    </div>

                    <div className="form-group">
                    <label className="" htmlFor="langue">Langue</label>
                        <select defaultValue={this.state.langue}  name="langue" className="form-control"   onChange={this.onChange.bind(this)} id="langue">
                            <option value="Wolof" >Wolof</option>
                            <option value="Français" >Français</option>
                            <option value="Serrer">Serrer</option>
                            <option value="Pular">Pular</option>
                            <option value="Manding">Manding</option>
                            <option value="Diola">Diola</option>
                        </select>
                    </div>


                    <div className="form-group">
                        <label className="" htmlFor="baseDeDonnees">Base de données des utilisateurs (excel/csv)</label>
                        <input required="required" type="file" name="baseDeDonnees"  id="baseDeDonnees" className="form-control"   onChange={this.choixFichier.bind(this)} />
                    </div>

                    <div className="form-group">
                        <label className="" htmlFor="fichierAudio">Fichier audio</label>
                        <input required="required" type="file" name="fichierAudio"  id="fichierAudio" className="form-control"   onChange={this.choixFichier.bind(this)} />
                    </div>
                   

                    <button onClick={this.envoyer.bind(this)} className="btn btn-primary float-right">
                        COMMANDER
                    </button>

                    
                    
                    </form>
                    <div className="text-danger">
                            {this.state.message}
                    </div>
                    </div>
                    </div>

                    

                </div>

                <div className="col-sm-6 mb-4">
                    {/* Approche */}
                    <div className="card shadow mb-4">
                    <div className="card-header py-3">
                        <h6 className="m-0 font-weight-bold text-primary">Approche</h6>
                    </div>
                    <div className="card-body">
                        <p>
                            Vous souhaitez envoyer une information urgente ? Vous souhaitez développer un service d’informations de perturbations ou d’incidents ? Avez-vous pensez à utiliser une alerte vocale dans la langue cible ?
                        </p>
                        <p className="mb-0">
                        La réalisation d’envoi d’alertes est une pratique approuvée, à la fois pratique, fiable, efficace et immédiate. Elle permet de surcroît de bénéficier d’un support universel où vos destinataires sont joignables à tout instant sur leur numéros de téléphone.
                        </p>
                    </div>
                    </div>
                </div>

                </div>

                </div>
                {/* /.container-fluid */}
                    </div>
        )
    }
  }
  
  export default Alerte;
