import React, {Component} from 'react';
import axios from 'axios';



import {
    //BrowserRouter as Router,
    //Switch,
    //Route,
    Link,
    Redirect
  } from "react-router-dom";


class Register extends Component{

   adresseAPI = localStorage.getItem("adresseApi");

  constructor(){
    super();

    this.state = {
      mail: '',
      prenom: '',
      nom: '',
      motdepasse: '',
      motdepasseconfirmer:'',
      telephone:'',
      registerTest:0,
      message:"",
    };

    //this.redirection();

    //this.recuperListUsers();
  }

  connecter(event){
    event.preventDefault();
    var self = this;
    var md5 = require('md5');

    self.setState({
        message: "Veuillez patienter pendant la verification..."
    })

    let donnees = {
        prenom: this.state.prenom, 
        nom: this.state.nom, 
        email: this.state.mail, 
        motdepasse: md5(this.state.motdepasse),
        motdepasseconfirmer: md5(this.state.motdepasseconfirmer),
        telephone: this.state.telephone,
    }

    if(donnees.motdepasse != donnees.motdepasseconfirmer){
      self.setState({
          message: "Les deux mots de passe que vous avez saisi ne correspondent pas!"
      })
      return 0;
    }

    if(!donnees.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i)){
      self.setState({
        message: "Adresse e-mail invalide!"
    })
    return 0;
    }

    axios.post(this.adresseAPI+`users_insert.php`, donnees, {headers: {'Content-Type':  'application/x-www-form-urlencoded'}})
        .then(function (reponse) {
           
            self.setState({
                message: reponse.data.message
            })

            if(reponse.data.etat=="bon"){
              self.remiseAzeroState();
              self.setState({
                  message: "Vos identifiants ont été créées avec succès."
              });

              setTimeout(() => {
                self.setState({
                  registerTest: 1
                });
                console.log(self.registerTest);
              }, 2000);
              
            }
            

            console.log(self.state);
           
        })
        .catch(function (error) {
            console.log("Erreur de conexion, l'api ne fonctionne pas");
            self.setState({
                message: "Erreur de conexion au serveur. Veuillez nous contacter!"
            })
            console.log(error)
        });

        


  }

  onChange(event){
    let name= event.target.name;
    let value= event.target.value;
    this.setState({
        [event.target.name]: event.target.value
    });

    
  }


  remiseAzeroState(){
    const keys = Object.keys(this.state)
    for (const key of keys) {
      this.setState({
        [key]: ""
      })
    }
  }

render(){
if(this.state.registerTest==1){
    return (
    <Redirect
        to={{
        pathname: "/login",
        state: { from: "register" }
        }}
    />
  )
}
return (
<div className="bg-gradient-primary-body">

<div className="container">

  <div className="row justify-content-center">

    <div className="col-xl-10 col-lg-12 col-md-9">

      <div className="card o-hidden border-0 shadow-lg my-5">
        <div className="card-body p-0">

          <div className="row">
            <div className="col-lg-6 d-none d-lg-block bg-login-image"></div>
            <div className="col-lg-6">
              <div className="p-5">
                <div className="text-center">
                  <h1 className="h4 text-gray-900 mb-4">Inscription!</h1>
                </div>
                <div className="text-danger">
                        {this.state.message}
                </div>
                <form className="user">
                <div className="form-group row">
                  <div className="col-sm-6 mb-3 mb-sm-0">
                    <input type="text" value={this.state.prenom} name="prenom" className="form-control form-control-user" id="exampleInputPrenom"  placeholder="Prénom" onChange={this.onChange.bind(this)} />
                  </div>
                  <div className="col-sm-6">
                    <input type="text" value={this.state.nom} name="nom" className="form-control form-control-user" id="exampleInputNom"  placeholder="Nom" onChange={this.onChange.bind(this)} />
                    </div>
                  </div>
                  
                  <div className="form-group">
                    <input type="text" value={this.state.mail} name="mail" className="form-control form-control-user" id="exampleInputEmail"  placeholder="Adresse e-mail" onChange={this.onChange.bind(this)} />
                  </div>

                  <div className="form-group">
                    <input value={this.state.telephone} type="number" name="telephone" className="form-control form-control-user" id="exampleInputTelephone"  placeholder="Téléphone / Exemple: 776698278" onChange={this.onChange.bind(this)} />
                  </div>

                  <div className="form-group row">
                  <div className="col-sm-6 mb-3 mb-sm-0">
                    <input value={this.state.motdepasse} type="password" name="motdepasse" className="form-control form-control-user" id="exampleInputPassword" placeholder="Mot de passe" onChange={this.onChange.bind(this)} />
                  </div>
                  <div className="col-sm-6">
                    <input value={this.state.motdepasseconfirmer} type="password" name="motdepasseconfirmer" className="form-control form-control-user" id="exampleInputPasswordConfirmer" placeholder="Confirmer mot de passe" onChange={this.onChange.bind(this)} />
                    </div>
                  </div>

                  
                  <div className="form-group">
                    <div className="custom-control custom-checkbox small">
                      <input type="checkbox" className="custom-control-input" id="customCheck"/>
                      <label className="custom-control-label" htmlFor="customCheck">Se souvenir de moi</label>
                    </div>
                  </div>
                  <button onClick={this.connecter.bind(this)} className="btn btn-primary btn-user btn-block">
                    CREER UN COMPTE
                  </button>
                  
                  <Link to="/login" className="btn btn-primary btn-user btn-block">
                    J'AI DEJA UN COMPTE
                  </Link>

                  <hr/>
                  
                </form>
                
                
                
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>

  </div>

</div>


</div>
   
  );

}
}

export default Register;