import React, {Component} from 'react';
import './App.css';
//import Entete from './include/entete';
import Login from './pages/login';
import Register from './pages/register';
import Logout from './logout';
import Controller from './controller';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  HashRouter
} from "react-router-dom";
import Accueil from './pages/accueil';
import Page from './pages/page';
import Alerte from './pages/alerte';
import Header from './include/header';
import Footer from './include/footer';

import {AuthProvider} from './authentificationContext'



class App extends Component {

  render() {
    localStorage.setItem("adresseApi", "https://niokomom.com/dawal/api/utilisateur/");
  return (
    <>

    <div className="wrapper home-one single-product-page">
    <HashRouter forceRefresh={true} >
    <AuthProvider>
    <Header/>
        <Switch>
          <Route path="/login" component={Login} />
          <Route path="/register" component={Register} />
          <Route path="/logout" component={Logout} />
          <Route path="/controller" component={Controller} />
          <Route path="/page" component={Page} />
          <Route path="/alerte" component={Alerte} />
          <Route path="/" component={Accueil} />
        </Switch>
      <Footer />
      </AuthProvider>
    </HashRouter>
    
    </div>

    
    </>
    
  );
  }
}

export default App;
