import React, {Component} from 'react';

import {
  Link
} from "react-router-dom";

class LogoutModal extends Component{

  constructor(){
    super();
    this.state = {
      
    }
  }
  



render(){
  return (
    <div className="">
        {/* Logout Modal*/}
        <div className="modal fade" id="logoutModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog" role="document">
            <div className="modal-content">
                <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">Prêt à partir?</h5>
                <button className="close" type="button" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">×</span>
                </button>
                </div>
                <div className="modal-body">Sélectionnez "Déconnexion" ci-dessous si vous êtes prêt à terminer votre session en cours. En espérant vous revoir bientôt!</div>
                <div className="modal-footer">
                <button className="btn btn-secondary" type="button" data-dismiss="modal">Annuler</button>
                <Link className="btn btn-primary" data-dismiss="modal" to="/logout">
                  <span>Déconnexion</span>
                </Link>
                
                </div>
            </div>
            </div>
        </div>
    </div>
  );

}
}

export default LogoutModal;