import React from 'react';
import {
  Redirect
} from "react-router-dom";

export default function VerifConnexion () {

    let infosUser = sessionStorage.getItem('infosUser');


    if(!infosUser){
        return (
            <Redirect
            to={{
            pathname: "/login",
            state: { from: "controller" }
            }}
            />
          )
    }else{
        return null
    }
    
}
