import React, {Component} from 'react';
import axios from 'axios';

import {
    Link,
    Redirect
  } from "react-router-dom";


class Login extends Component{

  adresseAPI = localStorage.getItem("adresseApi");

  constructor(){
    super();

    this.state = {
      mail: '',
      motdepasse: '',
      utilisateurs: [
      ],
      logintest:0,
      message:"",
    };

    //this.redirection();

    //this.recuperListUsers();
  }

  recuperListUsers() {
    axios.get(this.adresseAPI+`users_select.php`)
      .then(res => {
        const users = res.data;
        this.setState({ utilisateurs: users });
      })
  }

  connecter(event){
    event.preventDefault();
    var md5 = require('md5');


    this.setState({
        message: "Vérification identifiants en cours..."
    })
    let logintest=0;

    let donnees = {
        email: this.state.mail, 
        motdepasse: md5(this.state.motdepasse),
    }



    var self = this;

    
    

    axios.post(this.adresseAPI+`users_connect.php`, donnees, {headers: {'Content-Type':  'application/x-www-form-urlencoded'}})
        .then(function (reponse) {
            
           if(reponse.data.identifiant=="incorrect"){
            //self.props.history.push('./login');
            self.setState({
                message: "E-mail ou mot    passe incorrect. Veuillez rééssayer!"
            })
           }
           
           if(reponse.data.etat=="bon")
           {
            console.log(reponse.data);
            sessionStorage.setItem('infosUser', JSON.stringify(reponse.data.infosUser));
            sessionStorage.setItem('tokenUser', JSON.stringify(reponse.data.token));
            self.setState({
                logintest: 1,
                message: "Connexion réussie avec succès. Veuillez patienter pendant le chargement de la page!"
            })
               
           }
        })
        .catch(function (error) {
            console.log("Erreur de conexion, l'api ne fonctionne pas");
            self.setState({
              message: "Erreur de connexion au serveur: veuillez-nous contacter!"
          })
        });

  }

  onChange(event){
    this.setState({
        [event.target.name]: event.target.value
    });
  }

render(){
if(this.state.logintest==1){
    return (
    <Redirect
        to={{
        pathname: "./",
        state: { from: "login" }
        }}
    />
  )
}
return (
<div className="bg-gradient-primary-body">

<div className="container">

  <div className="row justify-content-center">

    <div className="col-xl-10 col-lg-12 col-md-9">

      <div className="card o-hidden border-0 shadow-lg my-5">
        <div className="card-body p-0">

          <div className="row">
            <div className="col-lg-6 d-none d-lg-block bg-login-image">

                <br></br>
                <br></br>
                    
                    
                    <Link to="./register" className="customer-btn">
                    CREER NOUVEAU COMPTE
                  </Link>

                    
            </div>
            <div className="col-lg-6">
              <div className="p-5">
                <div className="text-center">
                  <h1 className="h4 text-gray-900 mb-4">S'IDENTIFIER!</h1>
                </div>
                <div className="text-danger">
                        {this.state.message}
                </div>
                <form className="user">
                  <div className="form-group">
                    <input type="text" name="mail" className="form-control form-control-user" id="exampleInputEmail" aria-describedby="emailHelp" placeholder="Adresse e-mail" onChange={this.onChange.bind(this)} />
                  </div>
                  <div className="form-group">
                    <input type="password" name="motdepasse" className="form-control form-control-user" id="exampleInputPassword" placeholder="Mot de passe" onChange={this.onChange.bind(this)} />
                  </div>
                  <div className="form-group">
                    <div className="custom-control custom-checkbox small">
                      <input type="checkbox" className="custom-control-input" id="customCheck"/>
                      <label className="custom-control-label" htmlFor="customCheck">Se souvenir de moi</label>
                    </div>
                  </div>
                  <button onClick={this.connecter.bind(this)} className="btn btn-primary btn-user btn-block">
                    Valider
                  </button>
                  
                  <Link to="./register" className="btn btn-primary btn-user btn-block">
                    CREER UN COMPTE
                  </Link>
                  <hr/>
                  
                </form>
                
                <div className="text-center">
                  <a className="small" href="forgot-password.html">Mot de passe oublié?</a>
                </div>
                
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>

  </div>

</div>


</div>
   
  );

}
}

export default Login;